export const arg = (function(){
    const _pair=location.search.substring(1).split('&');
    var _arg;
    if(_pair[0] != '') {
        _arg = new Object;
        for(var i=0;_pair[i];i++) {
            var kv = _pair[i].split('=');
            _arg[kv[0]]=kv[1];
        }
    }
    return _arg;
})();